import {Button, Table} from "react-bootstrap";
import Alert from "./Alert";
import {useSelector} from "react-redux";
import {Link} from "react-router-dom";

function SawItem(props){
    let item = props.item;
    let sawItemState = JSON.parse(localStorage.getItem('sawItems'));
    return(
        <>
            <Table striped bordered hover>
                <thead>
                <tr>
                    <th>#</th>
                    <th>상품명</th>
                    <th>남은재고</th>
                </tr>
                </thead>
                <tbody>
                { sawItemState!==null ?
                    sawItemState.map((e,i) => {
                        return (
                            <tr key={e}>
                                <td><Link to={"/detail/"+e} ><img src={item[e].src} width='100' height='120'/></Link></td>
                                <td style={{verticalAlign:"middle"}}>{item[e].title}</td>
                                <td style={{verticalAlign:"middle"}}>{item[e].stock}</td>
                            </tr>
                        )
                    }) : null
                }
                </tbody>
            </Table>
        </>
    )
}

export default SawItem;