import {CSSTransition} from "react-transition-group";
import {useEffect, useState} from "react";
import {Nav} from "react-bootstrap";
import Cart from "./Cart";
import "../css/detail.scss";
import SawItem from "./SawItem";

function TabComponent(props){
    let [tab, setTab] = useState(0);
    let [tabSwitch, setTabSwitch] = useState(true);
    let item = props.item;
    return(
        <>
            <Nav variant="tabs" defaultActiveKey="link-0">
                <Nav.Item>
                    <Nav.Link eventKey="link-0" onClick={()=>{setTab(0); setTabSwitch(false)}}>장바구니</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={()=>{setTab(1); setTabSwitch(false)}}>봤던 상품 (최신3개)</Nav.Link>
                </Nav.Item>
                <Nav.Item>
{/*                    <Nav.Link eventKey="disabled" disabled>
                        봤던 상품은 최신 3개까지
                    </Nav.Link>*/}
                </Nav.Item>
            </Nav>
            <CSSTransition in={tabSwitch} classNames="itemDetailTab" timeout={500}>
                <TabContents tab={tab} setTabSwitch={setTabSwitch} item={item}/>
            </CSSTransition>
        </>
    )
}

function TabContents(props){
    let item = props.item;
    useEffect(()=>{
        props.setTabSwitch(true);
    })

    switch (props.tab){
        case 0 : return <Cart/>
        case 1 : return <SawItem item={item}/>
        default : return <div>장바구니에 아무것도 없습니다.</div>
    }
}

export default TabComponent;